<template>
  <el-dialog
    :title="!dataForm.id ? '方案新增' : '方案修改'"
    :close-on-click-modal="false"
    width="38%"
    :visible.sync="visible">
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="100px">
      <el-form-item label="方案名称" prop="schemeName">
        <el-input v-model="dataForm.schemeName" style="width: 400px;"></el-input>
      </el-form-item>
      <el-form-item label="是否公共" prop="schemeIsshare">
        <el-select v-model="dataForm.schemeIsshare" style="width: 400px;">
          <el-option v-for="item in schemeIsshare" :label="item.text" :key='item.id' :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="方案类型" prop="schemeType">
        <el-select v-model="dataForm.schemeType" style="width: 400px;">
          <el-option v-for="item in schemeType" :label="item.text" :key='item.id' :value="item.id"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        sysId:'',
        dataForm: {schemeId:0,schemeName:'',schemeIsshare:1,schemeType:0},
        dataRule: {
          schemeName: [
            { required: true, message: '方案名称必填', trigger: 'blur' }
          ]
        },
        schemeIsshare:[{id:0,text:'私有'},{id:1,text:'公共'}],
        schemeType:[
          {id:0,text:'人员信息校验'},
          {id:1,text:'公务员临时信息校验'}
        ]
      }
    },
    methods: {
      init(id) {
        this.dataForm.schemeId=id || 0
        this.visible = true;
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(this.dataForm.schemeId) {
            this.$http({
              url: this.$http.adornUrl(this.API.queryDataCheckschemeDetails),
              method: 'post',
              params: this.$http.adornParams({
                schemeId:id
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.dataForm.schemeId=data.data.schemeId;
                this.dataForm.schemeName=data.data.schemeName;
                this.dataForm.schemeIsshare=data.data.schemeIsshare?parseInt(data.data.schemeIsshare):1;
                this.dataForm.schemeType=data.data.schemeType?parseInt(data.data.schemeType):0;
              }
            })
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.dataForm.schemeId=this.dataForm.schemeId || undefined
            this.$http({
              url: this.$http.adornUrl(this.API[!this.dataForm.schemeId?'saveDataCheckscheme':'updateDataCheckscheme']),
              method:'post',
              data:this.$http.adornData(this.dataForm)
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            })
          }
        })
      }
    }
  }
</script>



