<template>
  <!-- 校验方案 -->
  <div>
    <el-form :inline="true" class="float-right">
      <el-form-item>
        <el-button type="primary" size="small" @click="addOrUpdateHandle()">添加</el-button>
        <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
        <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
        <el-button type="primary" size="small" @click="sortHanlde">保存排序</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      :height="siteContentViewHeight - 56 +'px'"
      @selection-change="selectionChangeHandle"
      style="width: 100%;">
      <el-table-column type="selection" width="40"></el-table-column>
      <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
      <el-table-column prop="schemeName" width="200" label="名称"></el-table-column>
      <el-table-column prop="schemeIsshare" width="100" label="是否公用">
        <template slot-scope="scope">
          <div v-if="parseInt(scope.row.schemeIsshare)===0">私有</div>
          <div v-if="parseInt(scope.row.schemeIsshare)===1">公用</div>
        </template>
      </el-table-column>
      <el-table-column prop="schemeType" label="方案类型">
        <template slot-scope="scope">
          <div v-if="parseInt(scope.row.schemeType)===0">人员信息校验</div>
          <div v-if="parseInt(scope.row.schemeType)===1">公务员临时信息校验</div>
        </template>
      </el-table-column>
      <el-table-column width="240" label="操作">
        <template slot-scope="scope">
          <el-button type="success" size="mini" plain @click="associationCheckRulesHandle(scope.row.schemeId,scope.row.schemeType)">关联规则</el-button>
          <el-button type="primary" size="mini" plain @click="addOrUpdateHandle(scope.row.schemeId)">修改</el-button>
          <el-button type="danger" size="mini" plain @click="deleteHandle(scope.$index,scope.row.schemeId)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 弹窗, 新增 / 修改 -->
    <add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>
    <!-- 弹窗，关联校验规则 -->
    <association-check-rules v-if="associationCheckRulesVisible" ref='associationCheckRules' @refreshDataList="getDataList"></association-check-rules>
  </div>
</template>

<script>
  import AddOrUpdate from './add-or-update'
  import {swapItems} from '@/utils';
  import associationCheckRules from '../checklist-management/association-check-rules.vue'
  export default{
    props:['siteContentViewHeight'],
    data(){
      return{
        dataList:[],
        dataListLoading:false,
        dataListSelections:[],
        addOrUpdateVisible: false,
        associationCheckRulesVisible:false
      }
    },
    components:{AddOrUpdate,associationCheckRules},
    created () {
      this.getDataList()
    },
    methods:{
      getDataList () {// 获取数据列表
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl(this.API.queryDataCheckscheme),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList=data.data;
          }else{
            this.$message.error(data.msg);
            this.dataList=[];
          }
          this.dataListLoading = false;
        });
      },
      addOrUpdateHandle(id){// 新增 / 修改
        this.addOrUpdateVisible = true
        this.$nextTick(() => {
          this.$refs.addOrUpdate.init(id)
        })
      },
      associationCheckRulesHandle(id,type){
        this.associationCheckRulesVisible = true;
        this.$nextTick(() => {
          this.$refs.associationCheckRules.init(id,type)
        })
      },
      selectionChangeHandle(val) {//多选
        this.dataListSelections=val;
      },
      moveDirection(direction){ //移动
      	var that=this,index;
      	that.dataListSelections.sort((prev,next)=>{
      		return that.dataList.indexOf(prev)-that.dataList.indexOf(next)
      	})
      	if(direction=='down') that.dataListSelections.reverse()
      	that.dataListSelections.forEach(item=>{
      		index=that.dataList.indexOf(item); //得到右侧选中值的标识坐标
      		if(direction=='up') that.moveUp(index);
      		else if(direction=='down') that.moveDown(index)
      	})
      },
      moveUp(index){//上移
      	if(index==0) return;
      	this.dataList=swapItems(this.dataList,index,index-1,'up');
      },
      moveDown(index){ //下移
      	if(index==this.dataList.length-1) return;
      	this.dataList=swapItems(this.dataList,index,index+1,'down');
      },
      sortHanlde(){//保存排序
        var sortArr=[];
        if(this.dataList.length<0) return;
        this.dataList.forEach((item,index)=>{
          sortArr.push({
            schemeId:item.schemeId,
            orderno:index+1
          })
        })
        this.$http({
          url: this.$http.adornUrl(this.API.dataCheckschemeSort),
          method: 'post',
          data: this.$http.adornData({
            data:JSON.stringify(sortArr)
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      deleteHandle(index,id){
        this.$confirm('确定要删除该信息？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API.deleteDataCheckscheme),
            method: 'post',
            data: this.$http.adornData({
              schemeId:id
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.dataList.splice(index,1);
              this.$message.success('操作成功');
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      }
    }
  }
</script>

<style scoped>
  .el-form--inline{padding-left: 10px;}
  .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}
</style>



